import React from "react";

interface Props {
  status: 0 | 1 | 2;
}

const TodoH2: React.FC<Props> = (props) => {
  const {status} = props;
  let text = "";

  switch (status) {
    case 0:
      text = "";
      break;
    case 1:
      text = "";
      break;
    case 2:
      text = "";
      break;
  }

  return <h2>{text}</h2>;
};

export default TodoH2;
